@import url('https://fonts.googleapis.com/css?family=Open+Sans|Playfair+Display+SC');

.App {
  padding: 1em;
}

label {
  display: inline-block;
  width: 100px;
}

h1{
  font-family: "Playfair Display SC";
  font-size: 64px;
  text-align: center;
}

header {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

/* typography */

html {
  font-family: 'helvetica neue', sans-serif;
}

thead th, tfoot th {
  font-family: 'Rock Salt';
}

th {
  letter-spacing: 2px;
  width: 10%;
}


tbody tr:nth-child(odd){
  background-color: #fff0c7;
}

td {
  letter-spacing: 1px;
  overflow-x:hidden;
  overflow-y:hidden;
  padding: 10px;
  text-align: center;
  width: 10%;
}

input{
  width: 80%
}

textarea{
  width: 80%;
}

img{
  width: 100%;
  height: 100%;
  object-fit: scale-down
}

/* table */

table {
  table-layout: fixed;
  width: 100%;
  border-collapse: collapse;
  overflow:hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
}

.content_td p {
  max-width: 100%;
  max-height: 100px;
  overflow-y: auto;
  text-overflow: ellipsis;
}



/* Column Size for thead */
th:nth-child(4),:nth-child(5){
  width: 30%;
}
/* Column size for tbody */

tr td:nth-child(4),:nth-child(5){
  width: 30%;
}